.about-container {
    display: grid;
    /* grid-template-columns: 35% 50%; */
    grid-gap: 15%;
    gap: 15%;
}


.about-me {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;

    display: grid;
    place-items: center;
}
#dark .about-me {
    background: linear-gradient(
        45deg,
        transparent,
        var(--dark-variant),
        transparent
    );
}
#light .about-me {
    background: linear-gradient(
        45deg,
        transparent,
        var(--light-variant),
        transparent
    );
}
.about-me-img {
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--main-transition);
}

.about-me-img:hover {
    transform: rotate(0);
}

.about-cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr) ;
    grid-gap: 1.5rem;
    gap: 1.5rem;
}
.about-card {
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
}
#dark .about-card {
    background: var(--dark-variant);
}
#light .about-card {
    background: var(--light-variant);
}
#dark .about-card:hover {
    background: transparent;
    cursor: default;
}
#light .about-card:hover {
    background: transparent;
    cursor: default;
}
#dark .about-card:hover {
    border-color: var(--primary-dark);
}
#light .about-card:hover {
    border-color: var(--primary-light);
}

.about-icon {
    
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

#dark .about-icon {
    color: var(--primary-dark);
}
#light .about-icon {
    color: var(--primary-light);
}
.about-card h5 {
    font-size: 0.95rem;
}
#dark .about-card h5 {
    color: var(--text-dark-title);
}
#light .about-card h5 {
    color: var(--text-light-title);
}

.about-card small {
    font-size: 0.7rem;
    
}
#dark .about-card small {
    color: var(--text-dark-content);
}
#light .about-card small {
    color: var(--text-light-content);
}
.about-content p {
    margin:  2rem 0 2.6rem ;
    
}
#dark .about-content p {
    color: var(--text-dark-content);
}
#light .about-content p {
    color: var(--text-light-content);
}


/* media queries medium devices */ 

@media screen and (max-width: 1024px) {
    .about-container {
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;
    }
    .about-me {
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about-content p {
        margin: 1rem 0 1.5rem;

    }
}


/* media queries small devices */ 

@media screen and (max-width: 600px) {
    .about-me {
        width: 68%;
        margin: 0 auto 3rem;
    }
    .about-cards {
        grid-template-columns: 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
    .about-content {
        text-align: center;
    }
    .about-content p {
        margin: 1.5em 0;
        }

}

.experience-container {
  display: grid;
  /* grid-template-columns: 1fr 1fr; */
  grid-gap: 2rem;
  gap: 2rem;
}

.experience-container > div {
  padding: 2.4rem 5rem;
  border-radius: 2rem;
  border: 1px solid transparent;
  transition: var(--main-transition);
}
#dark .experience-container > div {
  background: var(--dark-variant);
}
#light .experience-container > div {
  background: var(--light-variant);
}

#dark .experience-container > div:hover,
#light .experience-container > div:hover {
  background: transparent;
  cursor: default;
}
#dark .experience-container > div:hover {
  border-color: var(--primary-dark);
}
#light .experience-container > div:hover {
  border-color: var(--primary-light);
}
.experience-container > div h3 {
  text-align: center;
  margin-bottom: 2rem;
}
#dark .experience-container > div h3 {
  color: var(--primary-dark);
}
#light .experience-container > div h3 {
  color: var(--primary-light);
}
.experience-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-row-gap: 2rem;
  row-gap: 2rem;
}

.experience-details {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  justify-content: center;
}

.experience-details .icon {
  margin-top: 6px;
  color: var(--color-primary-variant);
  width: 2rem;
}
#dark .experience-details .icon {
  color: var(--variant-dark);
}
#light .experience-details .icon {
  color: var(--variant-light);
}
#dark .experience-details h4 {
  color: var(--text-dark-title);
}
#light .experience-details h4 {
  color: var(--text-light-title);
  width: 10rem;
}
#dark .experience-details small {
  color: var(--text-dark-content);
  font-size: 0.7rem;
}
#light .experience-details small {
  color: var(--text-light-content);
  font-size: 0.7rem;
}

/* media queries medium devices */

@media screen and (max-width: 1024px) {
  .experience-container {
    grid-template-columns: 1fr;
  }
  .experience-container > div {
    width: 80%;
    padding: 2rem;
    margin: 0 auto;
  }
  .experience-content {
    padding: 1rem;
  }
}

/* media queries small devices */

@media screen and (max-width: 600px) {
  .experience-container {
    grid-gap: 1rem;
    gap: 1rem;
  }
  .experience-container > div {
    width: 100%;
    padding: 2rem 1rem;
  }
  .experience-content {
    grid-template-columns: 0.3fr;
  }
}

